<template>
  <div class="tasteMethod-container">
    <headerUsually btnShow="true" titleText="" url="/mobileOrdering/SDishOperation"></headerUsually>
    <div class="getDish-con" v-if="condition">
      <div v-if="receiveItem.length>0">
        <div class="container-wrapper">
          <div class="left" ref="wrapperLeft">
            <ul class="container">
              <li
                class="left-item"
                :class="leftIndex==index?'left-item-active':''"
                v-for="(item, index) in receiveItem"
                :key="index"
                @click="leftClickFun(item,index)"
              >{{item.descript}}</li>
            </ul>
          </div>
          <div class="right" ref="wrapperRight">
            <ul class="container">
              <li
                class="right-item"
                :class="item.selected?'right-item-active':''"
                @click="rightClickFun(item,index)"
                v-for="(item, index) in rightArr"
                :key="index"
              >
                <span class="text" v-if="item.price>0">{{item.descript}} (+{{item.price}})</span>
                <span class="text" v-else>{{item.descript}}</span>
                <span class="iconfont icon-xuanzhong" v-if="item.selected"></span>
              </li>
            </ul>
          </div>
        </div>

        <div class="container-remarks">
          <textarea class="remarks" placeholder=":" v-model="newModifySDish.remark" />
        </div>
      </div>
      <div class="no-list" v-else>
        <noResult colors="rgba(57, 60, 65, 0.8)" text=""></noResult>
      </div>
    </div>
    <div class="privte-con" v-else>
      <div v-if="receiveItem.length>0">
        <div class="content-taste">
          <div class="wrapper" ref="wrapper">
            <ul class="container">
              <li
                class="right-item"
                :class="item.selected?'right-item-active':''"
                @click="SClickFun(item,index)"
                v-for="(item, index) in receiveItem"
                :key="index"
              >
                <span class="text" v-if="item.price>0">{{item.descript}} (+{{item.price}})</span>
                <span class="text" v-else>{{item.descript}}</span>
                <span class="iconfont icon-xuanzhong" v-if="item.selected==true"></span>
              </li>
              <!-- <li class="right-item right-item-active">
            <span class="text">11111</span>
            <span class="iconfont icon-xuanzhong"></span>
              </li>-->
            </ul>
          </div>
        </div>
      </div>
      <div class="no-list" v-else>
        <noResult colors="rgba(57, 60, 65, 0.8)" text=""></noResult>
      </div>
    </div>
    <div class="btn" @click="okFun()"></div>
  </div>
</template>
<script>
import ajaxGet from "@/conf/ajaxGet";
import { Toast } from "mint-ui";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
import BScroll from "better-scroll";
import noResult from "@/components/common/mobileOrdering/noResult.vue";
import { mapState, mapGetters } from "vuex";
export default {
  components: {
    headerUsually,
    noResult
  },
  data() {
    return {
      condition: true,
      receiveItem: {},
      scrollRight: {},
      rightArr: [],
      leftIndex: 0,
      newModifySDish: {},
      totalDishArr: []
    };
  },
  computed: {
    ...mapState({
      salesPoint: state => state.mobileOrdering.salesPoint,
      modifySDish: state => state.mobileOrdering.modifySDish
    })
  },
  async created() {
    this.newModifySDish = _.cloneDeep(this.modifySDish);
    // console.log("this.newModifySDish:", this.newModifySDish);
    if (this.newModifySDish.accnt) {
      this.condition = true;
      let data = {
        params: {
          pluCode: this.newModifySDish.code
        }
      };

      let res = await ajaxGet("/pos/router?method=findConddts", data);
      if (res && res.resultCode == 0) {
        this.receiveItem = res.data;
        this.receiveItem.forEach(element => {
          if (element.posConddtlList) {
            element.posConddtlList.forEach(ele => {
              if (this.newModifySDish.condCode.indexOf(ele.code) > -1) {
                this.$set(ele, "selected", true);
              } else {
                this.$set(ele, "selected", false);
              }
            });
          }
          this.rightArr = this.receiveItem[0].posConddtlList;
        });

        this.$nextTick(() => {
          if (this.receiveItem.length > 0) {
            let scrollLeft = new BScroll(this.$refs.wrapperLeft, {
              click: true
            });
            this.scrollRight = new BScroll(this.$refs.wrapperRight, {
              click: true
            });
          }
        });
      }
    } else {
      let dcgateway = localStorage.getItem("dcgateway")
        ? JSON.parse(localStorage.getItem("dcgateway"))
        : {};
      if (this.salesPoint.code == -1) {
        dcgateway.DishInfo.forEach((element, index) => {
          this.totalDishArr.push(...element.sortPluData);
        });
      } else {
        dcgateway.DishInfo.forEach(element => {
          if (element.pccode == this.salesPoint.code) {
            this.totalDishArr = element.sortPluData;
          }
        });
      }
      if (this.newModifySDish.flag.indexOf("1") == 1) {
        //
        this.condition = true;
        this.totalDishArr.forEach(ele => {
          if (ele.pccode == this.newModifySDish.pccode) {
            ele.pluDtos.forEach(opt => {
              if (
                opt.sortCode == this.newModifySDish.sortCode &&
                opt.pluCode == this.newModifySDish.pluCode
              ) {
                this.receiveItem = opt.posCondstDtoList;
                this.receiveItem.forEach(item => {
                  item.posConddtlList.forEach(li => {
                    if (this.newModifySDish.condCode.indexOf(li.code) > -1) {
                      this.$set(li, "selected", true);
                    } else {
                      this.$set(li, "selected", false);
                    }
                  });
                });
                console.log("this.receiveItem:", this.receiveItem);
                this.rightArr = this.receiveItem[0].posConddtlList;
                this.$nextTick(() => {
                  if (this.receiveItem.length > 0) {
                    let scrollLeft = new BScroll(this.$refs.wrapperLeft, {
                      click: true
                    });
                    this.scrollRight = new BScroll(this.$refs.wrapperRight, {
                      click: true
                    });
                  }
                });
              }
            });
          }
        });
      } else {
        if (this.newModifySDish.tasteType == "private") {
          //
          this.condition = false;
          this.totalDishArr.forEach(ele => {
            if (ele.pccode == this.newModifySDish.pccode) {
              ele.pluDtos.forEach(opt => {
                if (
                  opt.sortCode == this.newModifySDish.sortCode &&
                  opt.pluCode == this.newModifySDish.pluCode
                ) {
                  this.receiveItem = opt.pluCondgplist;
                  this.receiveItem.forEach(item => {
                    if (this.newModifySDish.condCode.indexOf(item.code) > -1) {
                      this.$set(item, "selected", true);
                    } else {
                      this.$set(item, "selected", false);
                    }
                  });
                  console.log("this.receiveItem:", this.receiveItem);
                  this.$nextTick(() => {
                    if (this.receiveItem.length > 0) {
                      let wrapper = new BScroll(this.$refs.wrapper, {
                        click: true
                      });
                    }
                  });
                }
              });
            }
          });
        }
      }
    }
  },
  mounted() {},
  methods: {
    leftClickFun(item, index) {
      if (this.leftIndex == index) {
        return;
      }
      this.leftIndex = index;
      this.rightArr = this.receiveItem[index].posConddtlList;
      this.$nextTick(() => {
        this.scrollRight.refresh();
        this.scrollRight.scrollTo(0, 0);
      });
    },
    rightClickFun(item, index) {
      item.selected = !item.selected;
    },
    SClickFun(item, index) {
      item.selected = !item.selected;
    },
    async okFun() {
      let cook = "";
      let codes = "";
      let cookList = [];
      if (this.newModifySDish.accnt) {
        // console.log("remark:", this.modifySDish.remark);
        this.receiveItem.forEach(element => {
          element.posConddtlList.forEach(ele => {
            if (ele.selected) {
              if (ele.price > 0) {
                cook += `${ele.descript}(${ele.price}),`;
              } else {
                cook += ele.descript + ",";
              }

              codes += ele.code + ",";
              ele.descriptEn = "";
              ele.unit = "";
              ele.number = this.newModifySDish.number;
              cookList.push(ele);
            }
          });
        });
        let data = {
          params: {
            proType: 3,
            pluState: this.newModifySDish.pluState,
            accnt: this.newModifySDish.accnt,
            orderId: this.newModifySDish.id,
            cook: cook,
            codes: codes,
            remark: this.newModifySDish.remark,
            cookList: JSON.stringify(cookList)
          }
        };

        let res = await ajaxGet("/pos/router?method=updateCheckDetail", data);
        if (res && res.resultCode == 0) {
          this.msg("");
          this.$router.push("/mobileOrdering/myOrder");
        }
      } else {
        if (this.newModifySDish.flag.indexOf("1") == 1) {
          //
          let dishSPrice = Number.parseFloat(this.newModifySDish.price);
          this.receiveItem.forEach(element => {
            element.posConddtlList.forEach(ele => {
              if (ele.selected) {
                cook += ele.descript + ",";
                codes += ele.code + ",";
                dishSPrice += Number.parseFloat(ele.price);
              }
            });
          });
          this.newModifySDish.remark = this.newModifySDish.remark;
          this.newModifySDish.amount = dishSPrice * this.newModifySDish.number;
          this.newModifySDish.cook = cook;
          this.newModifySDish.condCode = codes;
          this.$store.commit("changeTempTable", {
            type: "change",
            item: this.newModifySDish
          });
          // this.$router.push("/mobileOrdering/myOrder");
        } else {
          if (this.newModifySDish.tasteType == "private") {
            //
            let dishSPrice = Number.parseFloat(this.newModifySDish.price);
            this.receiveItem.forEach(ele => {
              if (ele.selected) {
                cook += ele.descript + ",";
                codes += ele.code + ",";
                dishSPrice += Number.parseFloat(ele.price);
              }
            });
            this.newModifySDish.amount =
              dishSPrice * this.newModifySDish.number;
            this.newModifySDish.cook = cook;
            this.newModifySDish.condCode = codes;
            this.$store.commit("changeTempTable", {
              type: "change",
              item: this.newModifySDish
            });
            //console.log("newModifySDish:", this.newModifySDish);
            //this.$router.push("/mobileOrdering/myOrder");
          }
        }
        this.$router.push("/mobileOrdering/myOrder");
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>
<style lang="less" scoped>
.tasteMethod-container {
  .getDish-con {
    .container-wrapper {
      background-color: #f6f8f9;
      position: absolute;
      width: 100%;
      top: 46px;
      bottom: 160px;
      overflow: hidden;
      display: flex;
      justify-content: space-between;
      .left {
        width: 140px;
        .left-item {
          height: 55px;
          line-height: 55px;
          text-align: left;
          font-size: 14px;
          color: #333333;
          padding-left: 13px;
          padding-right: 13px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        .left-item-active {
          background-color: #ffffff;
        }
      }
      .right {
        flex: 1;
        background-color: #fff;
        .right-item {
          padding-left: 15px;
          padding-right: 15px;
          height: 55px;
          line-height: 55px;
          font-size: 14px;
          color: #666666;
          border-bottom: 1px solid #ddd;
          display: flex;
          align-content: center;
          justify-content: space-between;
          background-color: #fff;
          font-size: 14px;
          .iconfont {
            display: none;
          }
        }
        .right-item-active {
          color: #c4ad68;
          .iconfont {
            display: block;
            color: #c4ad68;
          }
        }
      }
    }
  }
  .privte-con {
    .content-taste {
      background-color: #f6f8f9;
      position: absolute;
      top: 46px;
      bottom: 50px;
      overflow: hidden;
      display: flex;
      justify-content: space-between;
      .container {
        width: 100vw;
        .right-item {
          padding-left: 15px;
          padding-right: 15px;
          height: 55px;
          line-height: 55px;
          font-size: 14px;
          color: #666666;
          border-bottom: 1px solid #dddddd;
          display: flex;
          align-content: center;
          justify-content: space-between;
          background-color: #fff;
          font-size: 14px;
        }
        .right-item-active {
          color: #c4ad68;
          .iconfont {
            font-size: 18px;
            color: #c4ad68;
          }
        }
      }
    }
  }
  .container-remarks {
    position: fixed;
    bottom: 60px;
    width: 100%;
    .remarks {
      width: 100%;
      height: 80px;
      outline: none;
      border: none;
      padding: 10px;
      box-sizing: border-box;
    }
  }
  .no-list {
    margin-top: 150px;
  }
  .btn {
    position: fixed;
    bottom: 0;
    width: 100%;
    margin-top: 20px;
    background: #ffffff;
    font-size: 18px;
    color: #c4ad68;
    text-align: center;
    padding: 11px;
  }
}
</style>



